.cachu__container {
  height: 100%;
  width: 100%;
}

.cachu__container--content-fit .cachu__section {
  height: 100%;
}

.cachu__sections {
  visibility: hidden;
}

.cachu__section {
  position: relative;
  height: 100vh;
  width: 100%;
}

.cachu__nav {
  position: fixed;
  top: 50%;
  transform: translate3d(0, -50%, 0);
  z-index: 50;
  right: 17px;
}

.cachu__nav ul,
.cachu__nav li {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: center;
}

.cachu__nav ul li a {
  display: block;
  position: relative;
  width: .35rem;
  height: .35rem;
  margin: .6rem auto;
  cursor: pointer;
  text-decoration: none;
  transition: all .25s ease-in-out;
}

.cachu__nav ul li a.active {
  width: .8rem;
  height: .8rem;
}

.cachu__nav ul li a span {
  display: block;
  border-radius: 50%;
  width: 100%;
  height: 100%;
  border: 0;
  background: #FFFFFF;
}

.cachu__nav ul li .cachu__nav__tooltip {
  position: absolute;
  top: -2px;
  right: 20px;
  color: #fff;
  font-size: 14px;
  font-family: inherit;
  white-space: nowrap;
  max-width: 220px;
  overflow: hidden;
  display: block;
  opacity: 0;
  width: 0;
  cursor: pointer;
}
